<style scoped lang="less">
  @import "./release.less";
</style>
<template>
  <div class="release">
    <div class="left-panel">
      <mavon-editor ref="md" v-model="mdContent" :style="{height:mdHeight+'px'}" :toolbars="mdConfig" placeholder="..." :subfield="false" @change="change" @imgAdd="imgAdd" @imgDel="imgDel" :imageFilter="imgFilter"></mavon-editor>
    </div>
    <div class="right-panel">
      <Card dis-hover>
        <p slot="title"></p>
        <Form :model="releaseForm" :label-width="40">
          <FormItem label="">
            <Input v-model="releaseForm.title" placeholder=""></Input>
          </FormItem>
          <FormItem label="">
            <Select v-model="releaseForm.type" placeholder="">
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          <FormItem label="">
            <Select v-model="releaseForm.tag" placeholder="" multiple>
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="release"></Button>
            <!-- <Button type="ghost" style="margin-left: 8px"></Button> -->
          </FormItem>
        </Form>
      </Card>
    </div>
  </div>
</template>

<script>
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import mdConfig from '@/common/js/md-config' 
import {releaseArticle} from '@/api/article'
export default {
  name: 'release',
  data(){
    return{
      releaseForm:{
        title: '',
        type:'',
        tag: []
      },
      mdConfig: mdConfig, // markdown
      mdContent: '', // markdownmd
      // mdImgFile: {}
      mdImgFile: []
    }
  },
  computed:{
    mdHeight(){
      return window.innerHeight - (60+20)
    }
  },
  methods:{
    release(){
      // console.log(this.mdContent)
      // let markdownIt = mavonEditor.mixins[0].data().s_markdown // markdown-it
      // console.log(markdownIt.render(this.mdContent))  // mdhtml

      // FormDataget()getAll()
      let formData = new FormData()
      // Object.keys
      // if(Object.keys(this.mdImgFile).length){
      //   for(let item in this.mdImgFile){
      //     formData.append('file', this.mdImgFile[item])
      //   }
      // }
      if(this.mdImgFile.length){
        for(let item of this.mdImgFile){
          formData.append('file', item)
        }
      }
      formData.append('title', this.releaseForm.title)
      formData.append('type', this.releaseForm.type)
      formData.append('content', this.mdContent)
      // console.log(formData.getAll('file'))
      releaseArticle(formData).then(res => {
        console.log(res)
        if(res.data.success){
          this.mdContent = res.data.data.content
          this.$Message.success(res.data.msg)
        }else{
          this.$Message.error(res.data.msg)
        }
        
        // for(let item of res.data.data){
        //   this.$refs.md.$img2Url('./0',`${process.env.BASE_API}/${item.path}`)
        //   console.log(`${process.env.BASE_API}/${item.path}`)
        // }
        // if(res.data.success){
        //   this.modalLoading = false
        //   this.userModal = false
        //   this.resetForm()
        //   this._getUser()
        //   this.$Message.success(res.data.msg)
        // }else{
        //   this.modalLoading = false
        //   this.$Message.error(res.data.msg)
        // }
      })
    },
    // valuemdrenderhtml
    change(value, render){
      // console.log(value)
      // console.log(render)
    },
    imgFilter(file){
      console.log(file)
      return false
    },
    imgAdd(filename, file){
      console.log(filename)
      console.log(file)
      if(file.size > 500*1000){
        this.$Notice.warning({
          title: '',
          desc: ' ' + file.name + '  500kb'
        })
        return false
      }
      if(this.mdImgFile.length>20){
        this.$Notice.warning({
          title: '',
          desc: ' 20'
        })
        return false
      }
      // 
      // this.mdImgFile[filename] = file
      this.mdImgFile.push(file)
    },
    imgDel(filename){
      console.log(filename)
      delete this.mdImgFile[filename];
      console.log( this.mdImgFile)
    }
  },
  components: {
    mavonEditor
  }
}
</script>


